<template>
  <div class="pa-md-6 pa-sm-3">
    <v-btn
      depressed
      color="purple lighten-3"
      class="mr-3"
      @click="getRiwayat(0)"
      :dark="status===0?true:false"
      :outlined="status===0?false:true">
      menunggu pembayaran
    </v-btn>
    <v-btn
      depressed
      color="purple lighten-3"      
      @click="getRiwayat(1)"
      :dark="status===1?true:false"
      :outlined="status===1?false:true">
      transaksi berhasil
    </v-btn>
    <Loader v-if="isLoading" />
    <v-alert v-else-if="dataRiwayat.length === 0" class="mt-5" type="info">
      Data tidak ditemukan
    </v-alert>
    <Data v-else :data="dataRiwayat" :bank="dataBank" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import Loader from '@/components/Loader'
import Data from '../../components/Incubator/Riwayat/Data'
export default {
  data: () => ({
    status: 0
  }),
  components: {
    Data,
    Loader
  },
  created() {
    const id_paket = window.localStorage.getItem('id_paket')
    if (id_paket === null) {
      this.$store.dispatch('dataIncubatorOrder/getDataRiwayatPembayaran', {
        status: this.status
      })
    } else {
      this.$store.dispatch('dataIncubatorOrder/postDataPesan', {
        id_paket: id_paket
      })      
    }
    this.$store.dispatch('dataIncubatorOrder/updateIsLoading', true)
    window.localStorage.removeItem('id_paket')
  },
  computed: {
    ...mapState('dataIncubatorOrder', ['isLoading', 'dataRiwayat', 'dataBank'])
  },
  methods: {
    getRiwayat(val) {
      this.$store.dispatch('dataIncubatorOrder/getDataRiwayatPembayaran', {
        status: val
      })
      this.$store.dispatch('dataIncubatorOrder/updateIsLoading', true)
      this.status = val
    }
  }
}
</script>

